.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error {
        background-color: $rojo;
    }
    &.exito {
        background-color: $verde;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $verde;
        th {
            color: $blanco;
            padding: 2rem;
        }
    }
    tbody {
            tr{
                td{
                    text-align: center;
                }
            }
    }

    .imagen-tabla{
        margin: auto;
        width: 10rem;
        margin-bottom: 1rem;
    }

}

.imagen-small {
    width: 20rem;
    margin-bottom: 2rem;
}

.admin-bar {
    display: flex;
    justify-content: space-between;
}