.dark-mode {
    background-color: darken($grisOscuro, 10);
        p, a {
        color: $blanco;
    }
    h1,h2,h3,h4 {
        color: lighten($gris, 10);
    }
    .anuncio {
        background-color: $grisOscuro;
        border: 1px solid darken($gris, 10); 
    }

    .anuncio, .resumen-propiedad {
        .precio {
            color: $verde;
        }
        .icono {
            filter: invert(100);
        }
    }

    .texto-nosotros blockquote {
        color: $blanco;
    }

    .formulario {
        label, legend, p {
            color: $gris;
        } 

        input:not([type="submit"]),
    textarea, 
    select {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        color: $blanco;
    }
    }


}